import { Component } from '@angular/core';
import { NavController, NavParams, ModalController } from 'ionic-angular';
import { Values } from '../../providers/service/values';
import { Service } from '../../providers/service/service';

@Component({
    templateUrl: 'filter.html'
})
export class Filter {
    price: any = {lower: 0, upper: 1000};
    id: any;
    constructor(public nav: NavController, public navParams: NavParams, public modalCtrl: ModalController, public values: Values, public service: Service, ) {
        localStorage.setItem("pagename","filter");
 
    }
    ngOnInit() {
        if (this.values.filter.min_price) {
            this.price.lower = this.values.filter.min_price;
            this.price.upper = this.values.filter.max_price;
        } else this.price.upper = this.values.settings.max_price;
    }
    dismiss() {
        this.values.applyFilter = false;
        this.nav.pop();
    }
    applyFilter() {
        var i = 0;
        this.values.attributes.forEach((item, key) => {
            if (this.values.attributes[key].terms.length) this.values.attributes[key].terms.forEach(term => {
                if (term.selected && term.selected == true) {
                    this.values.filter['attributes' + i] = this.values.attributes[key].id;
                    this.values.filter['attribute_term' + i] = term.term_id;
                } else {
                    this.values.filter['attributes' + i] = undefined;
                    this.values.filter['attribute_term' + i] = undefined;
                }
                i++;
            });
        });
        this.values.filter.min_price = this.price.lower;
        this.values.filter.max_price = this.price.upper;
        this.values.applyFilter = true;
        this.nav.pop();
    }
    
}


