import { Component } from '@angular/core';
import {  NavController, NavParams,LoadingController,AlertController } from 'ionic-angular';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/forkJoin';

import { Http } from '@angular/http';
import { URLSearchParams } from '@angular/http';
import { Config } from '../../providers/service/config';


/**
 * Generated class for the PagesFeedbackPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-pages-feedback',
  templateUrl: 'pages-feedback.html',
})
export class PagesFeedbackPage {

mobile='';
message='';
loader:any;
feedback=false;
  constructor(public navCtrl: NavController, public navParams: NavParams,private loadingController: LoadingController,public alertCtrl: AlertController,public http:Http,public config:Config) {
  }

  presentAlert(message) {
    let alert = this.alertCtrl.create({
      title: message,
     
      buttons: ['Dismiss']
    });
    alert.present();
  }

  presentLoading(text) {
    this.loader = this.loadingController.create({
        content: text,
    });
    this.loader.present();
}
dismissLoading() {
    this.loader.dismiss();
}



  ionViewDidLoad() {
    console.log('ionViewDidLoad PagesFeedbackPage');
  }

send()
{
var validationmsg='';

if(this.mobile=='')
{
  validationmsg=validationmsg +"Mobile number is required <br>";


}
if(this.message=='')
{
  validationmsg=validationmsg +"Message is required <br>";


}

if(validationmsg!='')
{

this.presentAlert(validationmsg);
return;


}


this.presentLoading("Please wait");

var params = new URLSearchParams();
params.append("mobile", this.mobile);
params.append("message", this.message);
    return new Promise(resolve => {
    this.http.post(this.config.url + 'feedback.php', params, this.config.options).subscribe(data => {
      this.feedback=true;
      this.dismissLoading();
     
  
    }, err => {
      this.feedback=true;
      this.dismissLoading();

       // alert(JSON.parse(err._body))
    });
});



}



}



