import { Component } from '@angular/core';
import { NavController, Platform,Events  } from 'ionic-angular';
import { Service } from '../../../providers/service/service';
import { Functions } from '../../../providers/service/functions';
import { Values } from '../../../providers/service/values';
import { Home } from '../../home/home';
import { AccountForgotten } from '../forgotten/forgotten';
import { OneSignal } from '@ionic-native/onesignal';
import { Facebook } from '@ionic-native/facebook';

@Component({
    templateUrl: 'login.html'
})
export class AccountLogin {
    loginData: any;
    userInfo:any;
    loadLogin: any;
    status: any;
    error: any;
    nonce: any;
    fbusers: any;
    registerData: any;
fbuserid:any;
loginStatus: any;
    public disableSubmit: boolean = false;
    buttonText: any;
    errors: any;
    constructor(public events: Events,public nav: NavController, public service: Service, public functions: Functions, public values: Values, public platform: Platform, private oneSignal: OneSignal,public fb: Facebook) {
        localStorage.setItem("pagename","login");
        this.loginData = {};
        this.registerData = {};
        this.buttonText = "Login";
        this.service.getNonce()
            .then((results) => this.nonce = results);
    }

    ionViewWillEnter(): void {


        localStorage.setItem("pagename","login");
    
            //alert(localStorage.getItem("usermobile"));
    
         
        }


    login() {
        if (this.validateForm()) {
            this.disableSubmit = true;
            this.buttonText = "Logging In...";
            this.service.login(this.loginData)
                .then((results) => this.handleResults(results));
        }
    }
    validateForm() {
        if (this.loginData.username == undefined || this.loginData.username == "") {
            return false
        }
        if (this.loginData.password == undefined || this.loginData.password == "") {
            return false
        }
        else {
            return true
        }
    }
    handleResults(results) {
        this.disableSubmit = false;
        this.buttonText = "Login";
        if (!results.errors) {
            if (this.platform.is('cordova'))
            this.oneSignal.getIds().then((data: any) => {
                this.service.subscribeNotification(data);
            });
         
            this.nav.setRoot(Home);
        }
        else if (results.errors) {
            this.errors = results.errors;
        }
    }
    forgotten(loginData) {
        this.nav.push(AccountForgotten);
    }


    getUserDetail(userid) {
        this.fb.api("/"+userid+"/?fields=id,email,name,picture,gender",["public_profile"])
          .then(res => {
            //console.log(res);
            this.fbusers = res;
            this.events.publish('user:created', this.fbusers.email, Date.now());

//localStorage.setItem("fbname",this.fbusers.name);
//localStorage.setItem("fbemail",this.fbusers.email);
//localStorage.setItem("fblogin","Y");
var name=this.fbusers.name.split(" ");

this.registerData.username = this.fbusers.email;
this.registerData.email = this.fbusers.email;
this.registerData.first_name = name[0];
this.registerData.last_name = name[1];

this.service.registerCustomer(this.registerData)
.then((results) => this.handleRegister(results));
window.localStorage.setItem('username',this.fbusers.email);

this.nav.setRoot(Home);


     //  this.checkexist(this.fbusers.email);
      
      
      
          })
          .catch(e => {
           // console.log(e);
          });
      }


      fbloginfakelogin()
      {

        this.registerData.username = "ankitmishra333";
        this.registerData.email = "ankitmishra333@gmail.com";
        this.registerData.first_name = "Ankit";
        this.registerData.last_name = "Mishra";

        this.service.registerCustomer(this.registerData)
        .then((results) => this.handleRegister(results));

      }

      handleRegister(results) {

    //    console.log(JSON.stringify(results));
        this.disableSubmit = false;
        if (!results.errors) {
            this.service.login(this.registerData)
                .then((results) => this.loginStatus = results);
            if(this.platform.is('cordova')){
                this.oneSignal.getIds().then((data: any) => {
                    this.service.subscribeNotification(data);
                });
                this.oneSignal.sendTags({email: this.registerData.email });
            }
            this.nav.setRoot(Home);
        }
        else if (results.errors) {
            this.errors = results.errors;
        }
    }


        /*this.registerData.billing.email = this.registerData.email;
        this.registerData.billing.first_name = this.registerData.first_name;
        this.registerData.billing.last_name = this.registerData.last_name;*/


      
      

   fblogin()
   {

  this.fb.login(['public_profile',  'email'])
    .then(res => {
      if(res.status === "connected") {
       // this.isfbLoggedIn = true;
     //  alert(JSON.stringify(res.authResponse));
        this.getUserDetail(res.authResponse.userID);
      // this.spinnerDialog.hide();

             
      } else {
       // this.isfbLoggedIn = false;
       // this.spinnerDialog.hide();
      }
    })
    .catch(e => alert(JSON.stringify(e)));


   }


   loginphone(){
    (<any>window).AccountKitPlugin.loginWithPhoneNumber({
      useAccessToken: true,
      defaultCountryCode: "IN",
      facebookNotificationsEnabled: true,
    }, data => {
    (<any>window).AccountKitPlugin.getAccount(
      info => this.userInfo = info,
      err => alert(JSON.stringify(err)));
    });
  }




}


