import { Component } from '@angular/core';
import {  NavController, NavParams } from 'ionic-angular';
import { Service } from '../../providers/service/service';
import { NativeStorage } from '@ionic-native/native-storage';
import { ProductsPage } from '../products/products';
import { LoadingController } from 'ionic-angular';
import { CartPage } from '../cart/cart';
import { PagesUsers } from '../pages-users/pages-users';
import { Home } from '../home/home';

/**
 * Generated class for the CategoryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
 
  templateUrl: 'category.html',
})
export class Category{
  loader: any;
  items: any;
  constructor(private loadingController: LoadingController,public nav: NavController,private nativeStorage:NativeStorage,public navCtrl: NavController, public navParams: NavParams,public service: Service) {

    localStorage.setItem("pagename","category");
  }

 

bindcategories()
{
  localStorage.setItem("pagename","category");
  this.presentLoading("Please Wait");

  this.nativeStorage.getItem('blocks').then(data => { if (data) { 
    
    this.service.blocks = data.blocks;
    
    this.service.categories = data.categories.filter(item => item.name !== 'Uncategorized');
    this.service.mainCategories = this.service.categories.filter(item => item.parent === 0);
    
    } }, error => console.error(error));
    this.dismissLoading();

}

getCategory(id, slug, name) {
  this.items = [];
  this.items.id = id;
  this.items.slug = slug;
  this.items.name = name;
  this.items.categories = this.service.categories.filter(item => item.parent === parseInt(id));
  this.nav.setRoot(ProductsPage, this.items);
}


presentLoading(text) {
  this.loader = this.loadingController.create({
      content: text,
  });
  this.loader.present();
}
dismissLoading() {
  this.loader.dismiss();
}



openpage(page) {
  if(page=="quick")
  {
     
  
  window.open("https://www.homedelivery4u.com/quick-order",'_system', 'location=yes');
  }    

  if(page=="CategoryPage")

  {

location.reload(true);

  }

  if(page=="Home")
  {
  
    this.nav.setRoot(Home);
  
  }


  if(page=="CartPage")
  {
  
      this.nav.push(CartPage);
  
  }

  if(page=="PagesUsers")
  {

    this.nav.setRoot(PagesUsers);

  }

  else
  {
history.go(-1)
  }
}




}



