import { Component } from '@angular/core';
import {  NavController, NavParams,App } from 'ionic-angular';

import { Service } from '../../providers/service/service';

import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Config } from '../../providers/service/config';

import { Http } from '@angular/http';
import { URLSearchParams } from '@angular/http';

import { CartPage } from '../cart/cart';
import { LoadingController } from 'ionic-angular';

/**
 * Generated class for the WalletPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-wallet',
  templateUrl: 'wallet.html',
})
export class Wallet {
  topupflag:any;
  walletdetails:any;
  topupamount="";
  token:any;
  woo_wallet_topup:any;
  topupvalue="";
  loader:any;
  woo_wallet_topup_token="";

  constructor(private loadingController: LoadingController,private config: Config,public iab: InAppBrowser,public nav: NavController,public appCtrl: App,public service: Service,public navCtrl: NavController, public navParams: NavParams,private http: Http) {

    localStorage.setItem("pagename","wallet");
  }

  ionViewDidLoad() {
    localStorage.setItem("pagename","wallet");
    this.topupflag=false;

    this.service.getwallet()
    .then((results) =>{ this.walletdetails = results;
        console.log(this.walletdetails);
   window.localStorage.setItem("wallettoken",this.walletdetails.woo_wallet_topup);    
        });

  
  }


  presentLoading(text) {
    this.loader = this.loadingController.create({
        content: text,
    });
    this.loader.present();
}
dismissLoading() {
    this.loader.dismiss();
}


  addtopup()
  {



 
window.open("https://www.homedelivery4u.com/my-account/woo-wallet/add/",'_system', 'location=yes'); 
    
    


  }


  topup()
  {

    
    this.topupflag=true;

 
 

  }

 


}



