import { Component, ViewChild } from '@angular/core';
import { Platform, Nav, AlertController,Events,App   } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Home } from '../pages/home/home';
import { Service } from '../providers/service/service';
import { Values } from '../providers/service/values';
import { Config } from '../providers/service/config';
import { TranslateService } from '@ngx-translate/core';
import { OneSignal } from '@ionic-native/onesignal';
import { ProductsPage } from '../pages/products/products';
import { CartPage } from '../pages/cart/cart';
import { AccountLogin } from '../pages/account/login/login';
import { Address } from '../pages/account/address/address';
import { Orders } from '../pages/account/orders/orders';
import { AccountRegister } from '../pages/account/register/register';
import { OrderSummary } from '../pages/checkout/order-summary/order-summary';
import { WishlistPage } from '../pages/account/wishlist/wishlist';
import { ProductPage } from '../pages/product/product';
import { Post } from '../pages/post/post';
import { AppRate } from '@ionic-native/app-rate';
import { SocialSharing } from '@ionic-native/social-sharing';
import { EmailComposer } from '@ionic-native/email-composer';
import { NativeStorage } from '@ionic-native/native-storage';
import { Wallet } from '../pages/wallet/wallet';
import { PagesFeedbackPage } from '../pages/pages-feedback/pages-feedback';

@Component({
    templateUrl: 'app.html'
})
export class MyApp {
    @ViewChild(Nav) nav: Nav;
    rootPage: any = Home;
    status: any;
    lastBack : any;

    items: any = {};
walletbalance:any;

    buttonLanguagesSettings: boolean = false;
    constructor(public app: App,public events: Events,public statusBar: StatusBar, public splashScreen: SplashScreen, public alertCtrl: AlertController, public config: Config, private oneSignal: OneSignal, private emailComposer: EmailComposer, private appRate: AppRate, public platform: Platform, public service: Service, public values: Values, public translateService: TranslateService, private socialSharing: SocialSharing, private nativeStorage: NativeStorage) {
        platform.ready().then(() => {

            localStorage.setItem("pagename","home");

            this.initializeApp();

            this.splashScreen.hide();

            this.statusBar.overlaysWebView(false);
          //  this.statusBar.backgroundColorByHexString('#FEAE00');


          this.platform.registerBackButtonAction(()=>this.myHandlerFunction());



            this.service.load().then((results) => this.handleService(results));
            this.nativeStorage.getItem('blocks').then(data => { if (data) { 
                this.splashScreen.hide();
                this.service.blocks = data.blocks;
                this.values.settings = data.settings;
                this.service.categories = data.categories.filter(item => item.name !== 'Uncategorized');
                this.service.mainCategories = this.service.categories.filter(item => item.parent === 0);
                this.values.calc(platform.width());
                } }, error => console.error(error));




        });
      

    }
    initializeApp()
    {
this.getwallet();
        let username = window.localStorage.getItem('username') ? window.localStorage.getItem('username') : '';
    
    if(username!="")
    {

        this.values.isLoggedIn=true;
     

    }
    else
    {
        this.nav.setRoot(Home);
        this.values.isLoggedIn=false;
    }
        //  alert(username);



    }

    myHandlerFunction(){

     
     // let activeView = this.nav.getActive();
    
    
        if(localStorage.getItem("pagename")=='home' || localStorage.getItem("pagename") ==''   ){
      

            
     let alert = this.alertCtrl.create({
                 title: 'Exit',
                 message: 'Are you sure to exit app ?',
                 buttons: [
                    {
                         text: 'No',
                         role: 'cancel',
                         handler: () => {
                            // console.log('Buy clicked');
                         }
                     },
                     {
                         text: 'Yes',
                         
                         handler: () => {
                            this.platform.exitApp(); 
     
     
                         }
                     }
                     
                 ]
             });
            
             if(this.lastBack + 500 < Date.now())
            {
             alert.present();
     
            }
        
                               
                          }

                          if (this.nav.canGoBack()){
                            this.nav.pop();
                        }

                          else{
                            this.nav.setRoot(Home);
                          }
         
                          this.lastBack = Date.now();

     
          }
     


    handleService(results) {
        if (this.values.settings.app_dir == 'rtl') this.platform.setDir('rtl', true);
            this.translateService.setDefaultLang(this.values.settings.language);
        this.splashScreen.hide();
        this.values.calc(this.platform.width());
        if (this.platform.is('cordova') && this.values.settings.onesignal_app_id) {
            this.oneSignal.startInit(this.values.settings.onesignal_app_id, this.values.settings.google_project_id);
            this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.InAppAlert);
            this.oneSignal.handleNotificationReceived().subscribe(result => {
                console.log(result);
            });
            this.oneSignal.handleNotificationOpened().subscribe(result => {
                if (result.notification.payload.additionalData.category) {
                    this.nav.push(ProductsPage, {id: result.notification.payload.additionalData.category});
                } else if (result.notification.payload.additionalData.product) {
                    this.nav.push(ProductPage, result.notification.payload.additionalData.product);
                } else if (result.notification.payload.additionalData.post) {
                    this.post(result.notification.payload.additionalData.post);
                } else if (result.notification.payload.additionalData.order) {
                    this.nav.push(OrderSummary, {id: result.notification.payload.additionalData.order});
                }
            });
            this.oneSignal.endInit();
        }
    }
    openPage(page) {
        this.nav.setRoot(page);
    }
    getCategory(id, slug, name) {
        this.items = [];
        this.items.id = id;
        this.items.slug = slug;
        this.items.name = name;
        this.items.categories = this.service.categories.filter(item => item.parent === parseInt(id));
        this.nav.setRoot(ProductsPage, this.items);
    }
    getCart() {
        this.nav.setRoot(CartPage);
    }
    logout() {
        window.localStorage.setItem('username','');
        this.service.logout();
        this.values.wishlistId = [];
        this.nav.setRoot(AccountLogin);
    }
feedback()
{
this.nav.setRoot(PagesFeedbackPage);


}


    login() {
        this.nav.setRoot(AccountLogin);
    }
    register() {
        this.nav.setRoot(AccountRegister);
    }
    address() {
        this.nav.setRoot(Address);
    }
    order() {
        this.nav.setRoot(Orders);
    }
    cart() {
        this.nav.setRoot(CartPage);
    }
    wishlist() {
        this.nav.setRoot(WishlistPage);
    }
    shop() {
        this.nav.setRoot(Home);
    }

    
wallet()
{

    this.nav.push(Wallet);

}

getwallet()
{
    var userid=window.localStorage.getItem("userid");
    this.service.getwalletbalance(userid)
            .then((results) =>{ this.walletbalance = results;
               
                });


}


    rateApp() {
        if (this.platform.is('cordova')) {
            this.appRate.preferences.storeAppURL = {
                ios: this.values.settings.rate_app_ios_id,
                android:"https://play.google.com/store/apps/details?id=smart.webarts.deshimall", //this.values.settings.rate_app_android_id,
                windows: 'ms-windows-store://review/?ProductId=' + this.values.settings.rate_app_windows_id
            };
            this.appRate.promptForRating(true);
        }
    }
    shareApp() {
        if(this.platform.is('cordova')){
            var url = '';
            if(this.platform.is('android'))
            url ="https://play.google.com/store/apps/details?id=smart.webarts.deshimall"; //this.values.settings.share_app_android_link;
            else url = this.values.settings.share_app_ios_link;
            var options = {
                message: 'Install deshimall app for beytter experience',
                subject: '',
                files: ['', ''],
                url: url,
                chooserTitle: ''
            }
            this.socialSharing.shareWithOptions(options);
        }
    }
    contact() {
        let email = {
            to:"info@deshimall.in", //this.values.settings.support_email,
            subject: 'Contact Us',
            body: '',
            isHtml: true
        };
        this.emailComposer.open(email);
    }
    post(id) {
        this.nav.setRoot(Post, id);
    }
}


