import { Component } from '@angular/core';
import {  NavController, NavParams } from 'ionic-angular';
import { CartPage } from '../cart/cart';
import { Home } from '../home/home';
import { Category } from '../category/category';
import { AccountLogin } from '../account/login/login';
import { AccountRegister } from '../account/register/register';
import { Address } from '../account/address/address';
import { OrderSummary } from '../checkout/order-summary/order-summary';
import { WishlistPage } from '../account/wishlist/wishlist';
import { Values } from '../../providers/service/values';
import { Wallet } from '../wallet/wallet';
/**
 * Generated class for the PagesUsersPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  
  templateUrl: 'pages-users.html',
})
export class PagesUsers {

  isLoggedIn=false;
  constructor(public values: Values,public navCtrl: NavController, public navParams: NavParams) {
    
    localStorage.setItem("pagename","account");
  }

  ionViewDidLoad() {

 
   if(this.values.isLoggedIn)
    {

      this.isLoggedIn=true;



    }
    else
    {

      this.isLoggedIn=false;

    }

  

 
  }

  openpage(page) {



    

    if(page=="wallet")
  
    {
      this.navCtrl.push(Wallet);
        
    }



    if(page=="CategoryPage")
  
    {
  
      this.navCtrl.setRoot(Category);
  
    }
  
    if(page=="quick")
    {
       
    
    window.open("https://www.homedelivery4u.com/quick-order",'_system', 'location=yes');
    }    


    if(page=="Home")
    {
    
      this.navCtrl.setRoot(Home);
    
    }
  
  
    if(page=="CartPage")
    {
    
        this.navCtrl.push(CartPage);
    
    }
  
    if(page=="PagesUsersPage")
    {
  
      location.reload(true);
  
    }

    if(page=="login")
    {
    
        this.navCtrl.push(AccountLogin);
    
    }

    if(page=="register")
    {
    
        this.navCtrl.push(AccountRegister);
    
    }
    
    if(page=="address")
    {
    
        this.navCtrl.push(Address);
    
    }

    if(page=="orders")
    {
    
        this.navCtrl.push(OrderSummary);
    
    }

    if(page=="wishlist")
    {
    
        this.navCtrl.push(WishlistPage);
    
    }


  
    else
    {
  history.go(-1)
    }
  }
  


}



